module.exports = "import { Component, OnInit } from '@angular/core';\nimport { NzMessageService } from 'ng-zorro-antd';\n\n@Component({\n  selector: 'nz-demo-transfer-custom-item',\n  template: `\n    <nz-transfer\n      [nzDataSource]=\"list\"\n      [nzListStyle]=\"{'width.px': 300, 'height.px': 300}\"\n      [nzRender]=\"render\"\n      (nzSelectChange)=\"select($event)\"\n      (nzChange)=\"change($event)\">\n      <ng-template #render let-item>\n        <i nz-icon type=\"{{item.icon}}\"></i> {{ item.title }}\n      </ng-template>\n    </nz-transfer>\n  `\n})\nexport class NzDemoTransferCustomItemComponent implements OnInit {\n  // tslint:disable-next-line:no-any\n  list: any[] = [];\n\n  ngOnInit(): void {\n    this.getData();\n  }\n\n  getData(): void {\n    const ret = [];\n    for (let i = 0; i < 20; i++) {\n      ret.push({\n        key        : i.toString(),\n        title      : `content${i + 1}`,\n        description: `description of content${i + 1}`,\n        direction  : Math.random() * 2 > 1 ? 'right' : '',\n        icon       : `frown-o`\n      });\n    }\n    this.list = ret;\n  }\n\n  select(ret: {}): void {\n    console.log('nzSelectChange', ret);\n  }\n\n  change(ret: {}): void {\n    console.log('nzChange', ret);\n  }\n\n  constructor(public msg: NzMessageService) {\n  }\n}\n"